.bpk-calendar-week{display:flex;padding-top:1.5rem}.bpk-calendar-week__date{width:100%;text-align:center}.bpk-calendar-week__date--start{background-image:linear-gradient(to right, rgb(255, 255, 255) 50%, rgb(227, 240, 255) 50%)}html[dir=rtl] .bpk-calendar-week__date--start{background-image:linear-gradient(to left, rgb(255, 255, 255) 50%, rgb(227, 240, 255) 50%)}.bpk-calendar-week__date--middle{background-color:#e3f0ff}.bpk-calendar-week__date--end{background-image:linear-gradient(to left, rgb(255, 255, 255) 50%, rgb(227, 240, 255) 50%)}html[dir=rtl] .bpk-calendar-week__date--end{background-image:linear-gradient(to right, rgb(255, 255, 255) 50%, rgb(227, 240, 255) 50%)}